.portfolio-card {
  overflow: hidden;
  border-radius: 12px;
  box-sizing: border-box;
  position: relative;
  height: 150px;
  width: 300px;
  transition: 0.6s;
  box-shadow: 6px 6px -20px rgba(225, 225, 225, 0.5),
    -6px -6px 20px rgba(0, 0, 0, 0.1);
}

.portfolio-card:hover {
  height: 250px;
  box-shadow: 6px 6px -20px rgba(225, 225, 225, 0.1),
    -6px -6px 20px rgba(0, 0, 0, 0.05);
}

.portfolio-card:hover img {
  position: absolute;
  object-fit: cover;
}

.portfolio-card img {
  border-radius: 15px;
  transition: 0.6s;
  margin: auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlay-section {
  border-radius: 15px;

  -webkit-justify-content: space-between;

          justify-content: space-between;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #343a40;
  background-blend-mode: lighten;
  opacity: 0;
  transition: 0.6s;
  text-align: center;
}

.overlay-section p {
  padding: 0 10px;
  color: #f1f1f1;
}

.overlay-section h5 {
  margin-top: 10px;
}

.portfolio-card:hover .overlay-section {
  opacity: 1;
}

#portfolio {
  min-height: 80vh;
  background-color: white;
  background-blend-mode: hue;
}

#contact {
  background-color: #fff;
